import React, { useState } from 'react';
import { Box, Button, CircularProgress, Grid, Input, Typography } from '@mui/material';
import HelpOutlineSharpIcon from '@mui/icons-material/HelpOutlineSharp';

import axios from 'axios';
import './App.css'


const Weather = () =>{
    const [fiveDaysWeather, setFiveDaysWeather] = useState([])
    const [inputValue, setInputValue] = useState('')
    const [loading, setLoading] = useState(false)
    const weatherReport = async() =>{
        if(inputValue != ''){
        setLoading(true)
        setFiveDaysWeather([])
        await axios.get(`https://api.openweathermap.org/data/2.5/forecast?appid=15ca787f2d191cf1f09525804a2ce85d&q=${inputValue}`)
        .then((res) =>{
          let array = [];
          res.data.list.map((weather, index) =>{
            if(array.length < 5){
              const date = weather.dt_txt.split(' ')[0]
              const subArray = array.filter((weat) => weat.dt_txt.split(' ')[0] == date)
              if(subArray.length === 0){
                  array.push(weather)
                  setFiveDaysWeather((prevState) =>{ return[...prevState, weather] })
              }
            }else{
              return;
            }
          })
          setLoading(false)
        })
        .catch((err) =>{
          alert('please enter correct city name or something went wrong')
          setFiveDaysWeather([])
          setLoading(false)
        })
    }
      }

      const handleChange = (e) => {
        const inputValue = e.target.value;
        setInputValue(inputValue)
      };
    

  return(
    <Grid>
        <Grid style={{display: 'flex', justifyContent: 'center',marginBottom: '20px'}} className='flex-direction'>
      <Grid sx={{marginBottom: '20px'}}>
       <Typography sx={{ color: '#f96e0c', fontSize: '20px', fontWeight: 'bold', marginRight: '10px', marginTop: '15px'}}>Weather In Your City {inputValue}</Typography> 
      </Grid>
      <Grid style={{display: 'flex', justifyContent: 'center',alignItems: 'center'}} className='flex-direction'>
        <Input sx={{border: '2px solid #f96e0c', borderRadius: '5px', padding: '5px', height:'35px', maxWidth: '225px'}} onChange = {(e) =>{handleChange(e)}}/>
        <Button className='margin-button' onClick={(e) => weatherReport()} sx={{height:'35px', width: '100px', padding: '4px 12px',marginLeft: '10px', border: '2px solid #f96e0c', background: '#f96e0c', borderRadius: '5px', color: 'white'}}><HelpOutlineSharpIcon sx={{height: '20px'}}/> Search</Button>
      </Grid>
      </Grid>
    <Grid style={{display: 'flex', gap: '1em', flexWrap: 'wrap', justifyContent: 'center'}}>
    {
        loading ?
        <CircularProgress />
        :(
        fiveDaysWeather.map((day, index) =>{
            return (
            <Box sx={{width: '230px'}}>
                <Typography sx={{border: '1px solid black', background: '#f96e0c',fontWeight: 'bold'}}>Date: {`${day.dt_txt.split(' ')[0].split('-')[2]}/${day.dt_txt.split(' ')[0].split('-')[1]}/${day.dt_txt.split(' ')[0].split('-')[0]}`}</Typography>
                <Typography sx={{border: '1px solid black', background: '#b1a7a7', fontWeight: 'bold'}}>Temperature</Typography>
                <table style={{border: '1px solid black', borderCollapse: 'collapse'}}>
                <tr style={{background: '#b1a7a7'}}>
                    <th >Min</th>
                    <th>Max</th>
                </tr>
                <tr style={{background: '#ebd0c1'}}>
                    <td>{day.main.temp_min}</td>
                    <td>{day.main.temp_max}</td>
                </tr>
                <tr style={{background: '#ebd0c1'}}>
                    <td style={{fontWeight: 500}}>Pressure</td>
                    <td>{day.main.pressure}</td>
                </tr>
                <tr style={{background: '#ebd0c1'}}>
                    <td style={{fontWeight: 500}}>Humidity</td>
                    <td>{day.main.humidity}</td>
                </tr>
                
                </table>
            </Box>)
        })
   ) }
    </Grid>
    </Grid>
  )
}

export default Weather;